<template>
  <v-app>
    <rd-right-sidebar></rd-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs class="breadcrumbs">..</v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="networks"
              class="elevation-1"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
            >
              <template v-slot:top >
                <v-toolbar flat>
                  <v-toolbar-title>NETWORKS</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text" > Refresh <v-icon x-small right dark> mdi-refresh </v-icon> </v-btn>&nbsp;
                    <v-btn @click="dialog = true; tags = []" x-small color="success" class="white--text" > Create <v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text"> Delete <v-icon x-small right dark > mdi-delete </v-icon> </v-btn>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks,'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length !== 1" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;
                    <v-btn @click="onClickOpenDevicesView" x-small color="blue-grey" class="white--text" > View All Devices<v-icon x-small right dark> mdi-collage </v-icon> </v-btn>&nbsp;
                    <v-btn @click="onClickOpenPointsViewOfAllNetworks" x-small color="deep-purple" class="white--text" > View All Points<v-icon x-small right dark> mdi-view-headline </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>
              <template v-slot:item.created_on="{ item }" >
                {{item.created_on | timezone}}
              </template>
              <template v-slot:item.updated_on="{ item }" >
                {{item.updated_on | timezone}}
              </template>
              <template v-slot:item.name="{ item }">
                <a @click="onClickOpen(item)"> {{ item.name }} </a>
              </template>

              <template v-slot:item.history_enable="{ item }">
                <v-checkbox v-model="item.history_enable" @click="saveEdit(item)"></v-checkbox>
              </template>

              <template v-slot:item.enable="{ item }" >
                <v-checkbox v-model="item.enable" @click="saveEdit(item)"></v-checkbox>
              </template>

              <template v-slot:item.tags="{ item }">
                <ul style="cursor: pointer;" class="overflow-td" @click="editItem(item)" >
                  <li v-for="(key, index) of item.tags ? Object.keys(JSON.parse(item.tags)): []" :key="index">
                    {{key}} : {{JSON.parse(item.tags)[key]}}
                  </li>
                </ul>
              </template>

              <template v-slot:header.enable="{ item }" >
                <div class="d-flex">
                  <span>Enable</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.history_enable="{ item }" >
                <div class="d-flex">
                  <span>History enable</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.tags="{ item }" >
                <div class="d-flex">
                  <span>Tags</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('tags')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:item.actions="{ item }" >
                <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>
                <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="onClickOpenPointsView(item)">mdi-view-headline</v-icon>
                  </template>
                  <span>View all points</span>
                </v-tooltip>
              </template>
              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- create/edit dialog -->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="editedItem.name" label="Network name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.history_enable" label="History Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field  value="Tags" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <rd-point-tags v-if="dialog" :on-change="onChangeTag" :items="tags"></rd-point-tags>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport" >
            <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>

          </v-stepper-content><v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top >
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createNetwork(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }" >
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox v-if="typeof editAll[editField] == 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" small/>
          <rd-point-tags v-if="editField === 'tags'" :on-change="onChangeTag" :items="tags"></rd-point-tags>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from "../../utils/utils";
import axios from "../../axios";
import * as xlsx from "xlsx";
import _find from 'lodash/find';
import _every from 'lodash/every';
import _forIn from 'lodash/forIn';
import _forEach from 'lodash/forEach';
import _cloneDeep from 'lodash/cloneDeep';
import RdRightSidebar from './RdRightSidebar';
import MqttConsole from '../Mqtt/MqttConsole';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import RdPointTags from './RdPointTags';
import * as changeCase from "change-case";
import { DATA_TABLE_PROPS } from '../../constants/appConstant';


const pause = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export default {
  components: {
    MqttConsole,
    RdRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    RdPointTags
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    headers: [
      { text: "Actions", value: "actions", sortable: false },
      { text: "Name", value: 'name', width: '90px' },
      { text: "Enable", value: "enable", sortable: false },
      { text: "Driver", value: "driver", sortable: false },
      { text: "Fault", value: "fault", sortable: false },
      { text: "History Enable", value: "history_enable", sortable: false, width:'135px' },
      { text: "Tags", value: "tags", sortable: false, width: "150px" },
      { text: "Created On", value: "created_on", align: "start", sortable: false },
      { text: "Updated On", value: "updated_on", align: "start", sortable: false },
      { text: "UUID", value: "uuid", align: "start", sortable: false },
    ],
    networks: [],
    currentItem: {},
    editedIndex: -1,
    editedItem: {
      name: "",
      enable: false,
      tags: "",
    },
    defaultItem: {
      name: "",
      enable: false,
      tags: "",
    },
    rules: { required: (value) => !!value || "Required." },
    action: "",
    objectKeys: Object.keys,
    objectValues: Object.values,
    dataImport: "",
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    search:"",
    searchWizard:"",
    dialogMassEdit: false,
    editAll:{
      enable: false,
      history_enable: false,
      tags: [{}],
    },
    editField : "",
    intervalId: null,
    isClickSaveImport: false,
    networksIncludeChild: [],
    tags: [],
    tableProps: {...DATA_TABLE_PROPS},
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Network" : "Edit Network";
    },
    pingData(){
      return this.$store.state.pingData;
    },
    networksData () {
      const { rdNetworks } = this.$store.state;
      return rdNetworks;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$store.state.pingData':function(val){
      this.getPingData(val)
    },
    tags(val) {
      this.onChangeTag(val);
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getNetworks();
    if(Object.keys(this.pingData).length!==0){
      this.getPingData(this.pingData);
    }

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getNetworks();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getPingData(data){
      if(Object.keys(data).length===0){
          this.$toasted.show('Point Server is not running',{type:"error"});
          return;
      }
    },
    getNetworks() {
      axios.get( 'point-server/generic/network?with_children=true' )
        .then((data) => {
          this.networks = data;
          this.$store.commit('changeRdNetworksData', data);
        }).catch(({message}) => {
          this.$toasted.show(message, {type: 'error'});
        });
    },
    async getNetworkIncludeChild(item) {
      const network = await axios.get(`point-server/generic/network/${item.uuid}?with_children=true&points=true`);
      this.networksIncludeChild.push(network);
    },
    onClickOpen(item){
      this.$router.push({ path: `/rubix-driver/networks/${item.uuid}/devices` });
    },
    editItem(item) {
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
        const tags = JSON.parse(item.tags);
        for (const tag in tags) {
          arrTags.push({key: tag, value: tags[tag]});
        }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    saveEdit(item){
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.save();
    },
    save() {
      if (this.editedIndex > -1) {
        this.updateNetwork(this.editedItem);
      } else {
        this.createNetwork(this.editedItem);
      }
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.networks.indexOf(this.currentItem);
      this.networks.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/network/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          this.isClickSaveImport = true;
          try{
            const promises = [];
            for (const item of this.importArray) {
              if(!item.uuid){
                promises.push(new Promise((resolve, reject) => this.createNetwork(item, resolve, reject)));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    async clickImportJson(){
      this.networksIncludeChild = [];
      this.dialogImport = true;
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getNetworkIncludeChild(item));
      }
      await Promise.all(promises);
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.networksIncludeChild) : "";
      this.e1 = 1;
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(nw=>{
        delete nw.uuid;
        return nw;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      let networks = _cloneDeep(this.networks);
      if(this.editedIndex !== -1){ //when edit
        networks.splice(this.editedIndex, 1);
      }
      return _find(networks, {[fieldName]: value}) != null;;
    },
    convertExcelToJson(file){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
              }
              i.enable = i.enable ? i.enable : false;
              i.history_enable = i.history_enable ? i.history_enable : false;
              i.devices = this.getDevicesArray(i) || [];
              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    getDevicesArray(network){
      let devices = [];
      let deviceIndexs = [];
      let deviceKeys = [];
      //get deviceIndexs and deviceKeys
      Object.keys(network).forEach(key =>{
        let keys = key.split('.');
        let deviceKey = "";
        let deviceIndex = null;
        if(keys.length > 1){
          deviceKey = keys[1];
          if(!deviceKeys.includes(deviceKey)){
            deviceKeys.push(deviceKey);
          }
          deviceIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((deviceIndex || deviceIndex === 0) && !deviceIndexs.includes(deviceIndex)){
            deviceIndexs.push(deviceIndex);
          }
        }
      })
      //get device
      _forEach(deviceIndexs, i=>{
        let device = {};
        _forEach(deviceKeys, dk=>{
          device = {...device, [dk]: network[`device[${i}].${dk}`]};
        });
        //get points of device

        Object.keys(network).forEach(key =>{
          let keys = key.split('.');
          if(keys[0] == `device[${i}]` && keys.length > 2){
            device = {...device, [`${keys[1]}.${keys[2]}`]: network[key]};
            device.points = this.getPointsArray(device);
            device.enable = device.enable ? device.enable : false;
            device.history_enable = device.history_enable ? device.history_enable : false;
          }

        });
        devices.push(device);
      })
      return devices;
    },
    getPointsArray(device){
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key =>{
        let keys = key.split('.');
        let pointKey = "";
        let pointIndex = null;
        if(keys.length > 1){
          pointKey = keys[1];
          if(!pointKeys.includes(pointKey)){
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)){
            indexs.push(pointIndex);
          }
        }
      })
      _forEach(indexs, i=>{
        let point = {};
        _forEach(pointKeys,pk=>{
          point = {...point, [pk]: device[`point[${i}].${pk}`]}
        });
        point.priority_array_write = point.priority_array_write ? JSON.parse(point.priority_array_write): {};
        points.push(point);
      })
      return points;
    },
    async clickJsonExport(){
      this.networksIncludeChild = [];
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getNetworkIncludeChild(item));
      }
      await Promise.all(promises);
      const status = copyToClipboard(JSON.stringify(this.networksIncludeChild));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    async clickDownload(array, type){
      this.networksIncludeChild = [];
      const promises = [];
      for (const item of array) {
        promises.push(this.getNetworkIncludeChild(item));
      }
      await Promise.all(promises);
      const data = this.convertExportData(this.networksIncludeChild);
      if(type === "csv"){
        return exportCSV("networks", data);
      } if(type === "excel"){
        return exportExcel("networks", data);
      } if(type === "json"){
        return exportJSON("networks", JSON.stringify(this.networksIncludeChild));
      }
    },
    convertExportData(array){
      const data = _cloneDeep(array).map(network=>{
        _forEach(network.devices, (device, indexD)=>{
          //devices
          _forIn(device, (value, key) => {
            if(key != 'points'){
              const keyName = `device[${indexD}].${key}`;
              network = {...network, [keyName] : value};
            }else{
              //points
              _forEach(device.points, (point, indexP)=>{
                _forIn(point, (point_value, point_key) => {
                  if (point_key == 'priority_array_write') {
                    point_value = JSON.stringify(point_value);
                  }
                  if(point_key != 'point_store'){
                      const pointKey = `device[${indexD}].point[${indexP}].${point_key}`;
                      network = {...network, [pointKey] : point_value};
                  }else{
                    //point_store
                    _forIn(point.point_store, (point_store_value, point_store_key) => {
                      const pointStoreKey = `device[${indexD}].point[${indexP}].point_store.${point_store_key}`;
                      network = {...network, [pointStoreKey] : point_store_value};
                    });
                    delete point.point_store;
                  }
                });
              });
              delete device.points
            }
          });
        });
        delete network.devices
        return network;
      });
      return data;
    },
    updateNetwork(data) {
      data.name = changeCase[this.setting.caseType](data.name);
      axios.put(`point-server/generic/network/${data.uuid}`, data )
        .then((response) => {
          const index = this.networks.findIndex(n=> n.uuid == response.uuid);
          Object.assign(this.networks[index], response);
          this.updateRdNetworksData(data);
          this.close();
          this.$toasted.show("update success");
        })
        .catch((error) => { this.$toasted.show("update fail", {type: 'error'}); });
    },
    async createNetwork(data, resolve, reject) {
      try {
        data.name = changeCase[this.setting.caseType](data.name);
        const response = await axios.post(`point-server/generic/network`, data);
        this.networks.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if(index > -1){
            Object.assign(this.importArray[index], response);
          }
        }
        if(data.devices && data.devices.length > 0){
          this.createDevicesMulti(data.devices, response.uuid);
        }
        this.updateRdNetworksData(data);
        this.close();
        this.$toasted.show("create success");
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    async createDevicesMulti(devices, network_uuid){
      const promises = [];
      for (const device of devices) {
        device.network_uuid = network_uuid;
        device.address = device.address ? device.address : 0;
        promises.push(new Promise((resolve, reject) => this.createDevice(device, resolve, reject)));
      }
      await Promise.all(promises);
    },
    async createDevice(data, resolve, reject) {
      try {
        const response = await axios.post(`point-server/generic/device`, data);
        if (data.points && data.points.length > 0) {
          const promises = [];
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            promises.push(this.createPoint(point));
          }
          await Promise.all(promises);
        }
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    createPoint(data) {
      return axios.post(`point-server/generic/point`, data)
    },
    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor + 'set-row-height';
    },
    cancelEdit () {},
    openEdit () {},
    closeEdit () {},
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateNetwork(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      // this.selected = [];
      this.editAll= { enable: false, history_enable: false, tags: [{}], };
      this.editField = "";
      this.tags = [];
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      return toUpperTextKey(key);
    },
    onClickOpenPointsView(network){
      this.$router.push({ path:`/rubix-driver/networks/${network.uuid}/points-view` });
    },
    updateRdNetworksData(newNetwork){
      const indexNetWork = this.networksData.findIndex(nw=>nw.uuid == newNetwork.uuid);
      if(indexNetWork > -1){
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeRdNetworksData', this.networksData);
      }
    },
    onClickRefresh(){
      this.getNetworks();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onChangeTag(tags) {
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({...objTags});
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
    onClickOpenDevicesView(){
      this.$router.push({ path:`/rubix-driver/networks/devices-view` });
    },
    onClickOpenPointsViewOfAllNetworks(){
      this.$router.push({ path:`/rubix-driver/networks/all-networks-points-view` });
    },

  },

};
</script>
